@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:wght@700&display=swap');

:root {
    --background-dark: rgb(15, 0, 34);
    --background-light: rgb(56, 3, 124);
    --background-light-50: rgb(109, 46, 234);

    --white-color-1: white;
    --white-color-2: rgb(198, 198, 198);
    --white-color-3: rgba(223, 223, 223, 0.225);
    --subtitle-color : rgb(167, 173, 204);

    --primary-color-1: rgb(174, 0, 168);
    --primary-color-2: rgba(188, 2, 188, 0.86);
    --linear-background-1 : linear-gradient(0.25turn,var(--back-ground-1), var(--primary-color-1));
    --font-family: 'Roboto Condensed', sans-serif;

    --title-font-size-large: 40px;
    --title-font-size-small: 25px;

    --logo-size : 20px;

    --appbar-height: 40px;
    --page-padding-top: 100px;
}